import { createSlice } from "@reduxjs/toolkit";
import { config } from "../helpers/config";
const {
  DEPARTMENTS: { HEAD_OF_DESIGN, HEAD_OF_ENGINEERING, HEAD_OF_HR },
} = config;

const initState = {
  activeTeam: 0,
  teamList: {
    [HEAD_OF_HR]: [
      {
        value: "Team 1",
        depth: 2,
        isParent: true,
        showChild: false,
        childType: config.EMPLOYEES,
      },
      {
        value: "Team 2",
        depth: 2,
        isParent: true,
        showChild: false,
        childType: config.EMPLOYEES,
      },
    ],
    [HEAD_OF_ENGINEERING]: [
      {
        value: "Team 3",
        depth: 2,
        isParent: true,
        showChild: false,
        childType: config.EMPLOYEES,
      },
    ],
    [HEAD_OF_DESIGN]: [
      {
        value: "Team 4",
        depth: 2,
        isParent: true,
        showChild: false,
        childType: config.EMPLOYEES,
      },
    ],
  },
};

const teamSlice = createSlice({
  name: "teams",
  initialState: initState,
  reducers: {
    setShowEmployees: (state, action) => {
      const reqTeam = state.teamList[action.payload.parentElem.value].find(
        (team) => team.value === action.payload.elem.value
      );
      reqTeam.showChild = !reqTeam.showChild;
    },
    addNewTeam: (state, action) => {
      const {
        payload: { department, teamName },
      } = action;
      state.teamList[department].push({
        value: teamName,
        depth: 2,
        isParent: true,
        showChild: false,
        childType: config.EMPLOYEES,
      });
    },
  },
});

export const { setShowEmployees, addNewTeam } = teamSlice.actions;

export default teamSlice.reducer;
