import { createSlice } from "@reduxjs/toolkit";
import { config } from "../helpers/config";
const {
  DEPARTMENTS: { HEAD_OF_DESIGN, HEAD_OF_ENGINEERING, HEAD_OF_HR },
} = config;
const initState = {
  activeDepartment: 0,
  departmentList: [
    {
      value: HEAD_OF_HR,
      depth: 1,
      isParent: true,
      showChild: false,
      childType: config.TEAMS,
    },
    {
      value: HEAD_OF_ENGINEERING,
      depth: 1,
      isParent: true,
      showChild: false,
      childType: config.TEAMS,
    },
    {
      value: HEAD_OF_DESIGN,
      depth: 1,
      isParent: true,
      showChild: false,
      childType: config.TEAMS,
    },
  ],
};

const departmentSlice = createSlice({
  name: "departments",
  initialState: initState,
  reducers: {
    setShowTeams: (state, action) => {
      const reqDept = state.departmentList.find(
        (dept) => dept.value === action.payload
      );
      reqDept.showChild = !reqDept.showChild;
    },
  },
});

export const { setShowTeams } = departmentSlice.actions;

export default departmentSlice.reducer;
