import React from "react";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

function CommonSelectField(props) {
  const { label, options, onOptionChange, selected } = props;

  return (
    <div>
      <FormControl variant="outlined" size="small" className="wd-100">
        <InputLabel>{label}</InputLabel>
        <Select value={selected} onChange={onOptionChange} label={label}>
          {options.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label || option.value}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
  );
}

export default CommonSelectField;
