import React from "react";
import TextField from "@material-ui/core/TextField";

function CommonTextField(props) {
  const { onTextChange, label, value } = props;
  return (
    <>
      <TextField
        label={label}
        variant="outlined"
        size="small"
        value={value}
        onChange={onTextChange}
        className="wd-100"
      />
    </>
  );
}

export default CommonTextField;
