import React from "react";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";


function CustomCard(props) {
  const {
    title,
    children,
    leftActReq = false,
    leftActDetails = {},
    rightActReq = false,
    rightActDetails = {},
  } = props;
  return (
    <Card className="card">
      <CardContent>
        <div className="title">{title}</div>
        <div>{children}</div>
      </CardContent>
      <CardActions className="dsp-flex">
        {leftActReq && (
          <button onClick={leftActDetails.onClick}>
            {leftActDetails.title}
          </button>
        )}
        {rightActReq && (
          <button onClick={rightActDetails.onClick}>
            {rightActDetails.title}
          </button>
        )}
      </CardActions>
    </Card>
  );
}

export default CustomCard;
