import { useSelector, useDispatch } from "react-redux";
import React, { useState } from "react";
import Backdrop from "../common/Backdrop";
import Card from "../common/Card";
import TextField from "../common/TextField";
import SelectField from "../common/SelectField";
import { addNewTeam } from "../../store/teams";
import { addNewEmployee } from "../../store/employees";

let teamDetails = { team: {}, leader: {}, employee: {} };

function AddTeam() {
  const [openForm, setOpenForm] = useState(false);
  const [formType, setFormType] = useState("team");
  const dispatch = useDispatch();

  const addTeamToStore = () => {
    dispatch(addNewTeam(teamDetails.team));
  };

  const EmployeeDetailsForm = () => {
    const [name, setName] = useState("");
    const [id, setId] = useState("");
    const [email, setEmail] = useState("");
    const [phoneNo, setPhoneNo] = useState("");

    const onNameChange = (event) => {
      setName(event.target.value);
      teamDetails[formType].name = event.target.value;
    };

    const onIdChange = (event) => {
      setId(event.target.value);
      teamDetails[formType].id = event.target.value;
    };

    const onEmailChange = (event) => {
      setEmail(event.target.value);
      teamDetails[formType].email = event.target.value;
    };

    const onPhoneNoChange = (event) => {
      setPhoneNo(event.target.value);
      teamDetails[formType].phoneNo = event.target.value;
    };
    return (
      <>
        <div className="mr-t-1">
          <TextField onTextChange={onNameChange} value={name} label="Name" />
        </div>
        <div className="mr-t-1">
          <TextField onTextChange={onIdChange} value={id} label="Id" />
        </div>
        <div className="mr-t-1">
          <TextField
            onTextChange={onEmailChange}
            value={email}
            label="Email ID"
          />
        </div>
        <div className="mr-t-1">
          <TextField
            onTextChange={onPhoneNoChange}
            value={phoneNo}
            label="Phone Number"
          />
        </div>
      </>
    );
  };

  const AddEmployee = () => {
    const onFormSubmit = () => {
      setFormType("team");
      setOpenForm(false);
      dispatch(addNewTeam(teamDetails.team));
      dispatch(
        addNewEmployee({
          ...teamDetails.leader,
          isTeamLeader: true,
          teamName: teamDetails.team.teamName,
        })
      );
      dispatch(
        addNewEmployee({
          ...teamDetails.employee,
          isTeamLeader: false,
          teamName: teamDetails.team.teamName,
        })
      );
      teamDetails = { team: {}, leader: {}, employee: {} };
    };
    return (
      <Card
        title="Add Employee (3/3)"
        leftActReq={true}
        leftActDetails={{
          title: "back",
          onClick: () => {
            setFormType("leader");
          },
        }}
        rightActReq={true}
        rightActDetails={{
          title: "submit",
          onClick: onFormSubmit,
        }}
      >
        <EmployeeDetailsForm />
      </Card>
    );
  };

  const AddTeamLeader = () => {
    return (
      <Card
        title="Add Team Leader (2/3)"
        leftActReq={true}
        leftActDetails={{
          title: "back",
          onClick: () => {
            setFormType("team");
          },
        }}
        rightActReq={true}
        rightActDetails={{
          title: "next",
          onClick: () => {
            setFormType("employee");
          },
        }}
      >
        <EmployeeDetailsForm />
      </Card>
    );
  };

  const AddTeamForm = () => {
    const departments = useSelector(
      (state) => state.departments.departmentList
    );
    const [teamName, setTeamName] = useState("");
    const [dept, setDept] = useState("");
    const onInputChange = (event) => {
      setTeamName(event.target.value);
      teamDetails[formType].teamName = event.target.value;
    };
    const onDeptChange = (event) => {
      setDept(event.target.value);
      teamDetails[formType].department = event.target.value;
    };
    return (
      <Card
        title="Add Team (1/3)"
        leftActReq={true}
        leftActDetails={{
          title: "next",
          onClick: () => {
            setFormType("leader");
          },
        }}
      >
        <div className="mr-t-1">
          <SelectField
            options={departments}
            onOptionChange={onDeptChange}
            selected={dept}
          />
        </div>
        <div className="mr-t-1">
          <TextField
            onTextChange={onInputChange}
            value={teamName}
            label="Team Name"
          />
        </div>
      </Card>
    );
  };

  return (
    <>
      <button onClick={() => setOpenForm(!openForm)}>Add team</button>
      <Backdrop open={openForm}>
        {
          {
            team: <AddTeamForm />,
            leader: <AddTeamLeader />,
            employee: <AddEmployee />,
          }[formType]
        }
      </Backdrop>
    </>
  );
}

export default AddTeam;
